@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=ZCOOL+KuaiLe&display=swap);
.header {
  flex: 1 1;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: black;
}
.header i {
  top: 50%;
  display: inline;
  font-size: 30px;
  margin: 10px;
  color: #0f1338;
}

.header h1 {
  top: 50%;
  display: inline;
  font-size: 40px;
  margin: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Abel';
}
.main {
  flex: 4 1;
  height: 500px;
  display: flex;
  flex-direction: row;
}

.left-box {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

#code-editor {
  font-family: "Roboto Mono";
  background-color: #fafafa;
  width: 100%;
  height: 400px;
  margin: 20px 0;
  border-radius: .25rem;
  position: relative;
}

.codeflask {
  border-radius: .25rem;
  transition: all .05s ease-in;
  font-size: 20px !important;
}

.codeflask:focus-within {
  box-shadow: 0 0 0 5px rgb(129, 187, 253);
  -moz-outline-radius: .25rem;
}

.codeflask__flatten {
  font-size: 15px !important;
}

.buttons {
  width: 80%;
}

.buttons button {
  width: 100%;
  margin: 10px
}

.right-box h2 {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  margin-bottom: 20px;
  font-family: Abel;
}

.token-table-container {
  width: 100%;
  background-color: #fafafa;
  height: 400px;
  padding: 20px;
  border-radius: .25rem;
  overflow-y: scroll;
  scrollbar-width: none;
}

.token-box::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar {
  display: none;
}

.token-table-container table {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.footer {
  height: 60px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  align-items: flex-end;
  font-family: 'Balsamiq Sans';
  bottom: 15px;
  right: 15px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.App {
  /* background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%); */
  background: rgb(238,174,202);
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
  display: flex;
  width: 100%;
  height: 100vh;
  min-height: 750px;
  flex-direction: column;
}
